import {React,useState,useRef} from 'react';
import './Blog.css';
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from '@material-ui/core/TextField';
import Bounce from "react-reveal/Bounce";
import Button from "@material-ui/core/Button";
import Grid from '@material-ui/core/Grid';
import {makeStyles} from '@material-ui/core/styles';
import emailjs from '@emailjs/browser';
import{ init } from '@emailjs/browser';

import {HashLink} from 'react-router-hash-link';

import Typography from '@material-ui/core/Typography';

const useStyles = makeStyles((theme) => ({
    footerWrap:{
        padding: '30px',

        width:'100%',

        backgroundColor:'#fafafa',
    },
    copyrights:{
    width:'100%',
    textAlign:'center',
    fontSize:'1rem',
    marginTop:'30px'
    },
    form:{
    paddingTop: '15px'
    },
    formWrap:{
    },
    lastArticlesItem:{
    color:'#686868',
    },
    submit: {
        marginTop: '20px'
    },
}));
function BlogFooter(props){
init("user_bxfF5TvWm8i0NjmvGZUHZ");
  const form = useRef();

let social = [
{header:'פייסבוק >',
link:'https://www.facebook.com/profile.php?id=100004539256630'
},
{header:'אינסטגרם >',
link:'https://www.instagram.com/ofirthefreelancer/'
},
{header:'גיט >',
link:'https://github.com/ofirthefreelancer/'
},
{header:'וואצאפ >',
link:'https://api.whatsapp.com/send?phone=+972544626414&text=שלום (:'
},
];
    const classes = useStyles();
    const [submitted, setSubmitted] = useState(false);
    const [name, setName] = useState("");
    const [lastName, setLastName] = useState("");
    const [email, setEmail] = useState("");
    const [error, setError] = useState(false);
    const [message, setMessage] = useState("");
  const sendEmail = (e) => {
    e.preventDefault();
       setError(false);
    if (/^[a-zA-Z0-9]+@[a-zA-Z0-9]+\.[A-Za-z]+$/.test(email)) {
                      e.preventDefault();
                      if (name !== "" && lastName !== "" && email !== "" && message !== "") {
                              emailjs.sendForm('service_2exucqj', 'template_xlzfapp', e.target,'user_bxfF5TvWm8i0NjmvGZUHZ')
                                .then((result) => {
                                   setSubmitted(true);
                                    console.log(result.text);
                                }, (error) => {
                                    console.log(error.text);
                                });

                      } else {
                          setError(true);
                      }
            }
      else{
         setError(true);
      }
    }
    return (
        <div className={classes.footerWrap}>
               <Grid  direction="row" alignItems="stretch" container
                   spacing={8}>
                <Grid item xs={12} sm={12} md={4} >
                            <div className={classes.formWrap}>
                    <CssBaseline />
                    <div className={classes.paper}>
                        {!submitted && (
                            <Bounce>
                             <strong>
                                <div id="lets-contact">
                                    בואו נדבר
                                </div>
                                </strong>
                                <form
                                    onSubmit={sendEmail}
                                    className={classes.form}
                                    noValidate
                                    ref={form}
                                >
                                    <Grid container spacing={2}>
                                        <Grid item xs={12} sm={6}>
                                            <TextField
                                                autoComplete="fname"
                                                name="from_name"
                                                variant="outlined"
                                                required
                                                onChange={(e) => setName(e.target.value)}
                                                fullWidth
                                                id="from_name"
                                                placeholder="שם פרטי"
                                            />
                                        </Grid>
                                        <Grid item xs={12} sm={6}>
                                            <TextField
                                                variant="outlined"
                                                required
                                                fullWidth
                                                id="from_lastname"
                                                onChange={(e) => setLastName(e.target.value)}
                                                placeholder="שם משפחה"
                                                name="from_lastname"
                                                autoComplete="lname"
                                            />
                                        </Grid>
                                        <Grid item xs={12}>
                                            <TextField
                                                variant="outlined"
                                                required
                                                fullWidth
                                                onChange={(e) => setEmail(e.target.value)}
                                                id="reply_to"
                                                placeholder="אימייל"
                                                name="reply_to"
                                                autoComplete="email"
                                            />
                                        </Grid>
                                        <Grid item xs={12}>
                                            <TextField
                                                placeholder="כתוב לי משהו..."
                                                id="message"
                                                multiline
                                                onChange={(e) => setMessage(e.target.value)}
                                                fullWidth
                                                name="message"
                                                minRows={10}
                                                maxRows={10}
                                            />
                                        </Grid>
                                    </Grid>
                                    <Button
                                        type="submit"
                                        variant="outlined"
                                        color="secondary"
                                        className={classes.submit}
                                    >
                                        שלח
                                    </Button>
                                    {error && (
                                        <Typography>חלק מהפרטים חסרים</Typography>
                                    )}
                                </form>
                            </Bounce>
                        )}
                        {submitted && (
                            <div>
                                {" "}
                                <Typography variant="h5">
                                   תודה , הטופס נשלח בהצלחה.
                                </Typography>
                            </div>
                        )}
                    </div>
                          </div>
                </Grid>

            <Grid item xs={12} sm={4} md={3}>
            <div className={classes.socialLinks}>
               <strong> סושיאל </strong>
                  <div className={classes.lastArticles}>

                             {social && social.length > 0 ? (
                            social.map((social) => {

                            return <div key={social.header} style={{paddingTop:'5px'}}><a target="_blank" rel="noreferrer" href={social.link}  className={classes.lastArticlesItem} > {social.header}</a></div>
                            })) : ''}
                             </div>
            </div>
            </Grid>
            <Grid item xs={12} sm={8} md={5}>
            <div className={classes.lastArticles}>
               <strong> מאמרים אחרונים </strong>
                <div className={classes.lastArticles}>
                {props.lastPosts && props.lastPosts.length > 0 ? (

               props.lastPosts.map((post) => {

               return <div key={post.id} style={{paddingTop:'5px'}}><HashLink  className={classes.lastArticlesItem} to={`/blog/post/${post.id}`}> {post.postTitle}</HashLink></div>
               })) : ''}
                </div>
            </div>
            </Grid>
            </Grid>
              <div className={classes.copyrights}>כל הזכויות שמורות © אופיר כהן 2022 </div>
        </div>

    );
}
export default BlogFooter;